import { createMuiTheme } from "@material-ui/core/styles";

export const specials = {
  light: {
    aboutBg: "#f9f9f9",
  },
};
export const lightTheme = createMuiTheme({
  cards: {
    bgColor: "#fff",
    headerColor: "initial",
  },
  toolBar:{
    backgroundColor:'white',
    width:'100%'
  },
  appBar: {
    backgroundColor:'white',
    position: 'fixed',
    flexDirection: 'row',
    paddingLeft: '20px',
    paddingRight: '20px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between'
  },
  blogPostContent: {
  backgroundColor:'white',
    paddingRight: '20px',
    paddingTop: '100px',
    flexDirection: 'column',
    width: '65%',
    display: 'flex',
    position: 'absolute',
    justifyContent: 'start',
    alignItems: 'start',
  },
  blog:{
    backgroundColor:'white',
    overflowX:'hidden',
    margin: '0',
    fontSize: '1.3rem',
    fontFamily: "OpenSansHebrew",
    color: 'rgba(0,0,0,.8)',
    direction: 'rtl',
    textAlign: 'right',
  },
  postToolBar:{
    display:'flex',
    width:'100%'
  },
  postHeader:{
    display:'flex',
    width:'100%'
  },
  arrowColor:{
    fill:'black',
    cursor:'pointer'
  },

  bar:{
    color:'black',
    display: 'flex',
  },
  barItem:{
    color:'black',
    paddingLeft:'15px'
  },
  aboutBg: "#f9f9f9",
  palette: {
    type: "light",
    primary: {
      main: "#00adff",
      contrastText: "#fff", //button text white instead of black
    },
    background: {
      //3b457d
      default: "#ffffff",
    },
  },

  overrides: {
    MuiButton: {
      root: {
        textTransform: "none",
      },
      label: {
        color: "#000",
      },
    },
  },
});

export const darkTheme = createMuiTheme({
  cards: {
    bgColor: "#2c2c2c",
    headerColor: "#bbb",
  },
  palette: {
    type: "dark",
  },
  arrowColor:{
    fill:'white',
    cursor:'pointer'
  },
  blogPostContent:{
    color:'white',
    backgroundColor:'#303030',
    paddingRight: '20px',
    paddingTop: '100px',
    flexDirection: 'column',
    width: '65%',
    display: 'flex',
    position: 'absolute',
    justifyContent: 'start',
    alignItems: 'start',
  },
  toolBar:{
    backgroundColor:'#303030',
    width:'100%',
    color:'white'
  },
  blogPostHeader:{
    color:'white'
  },
  blogPostTitle:{
    color:'white'
  },
  blogPostDesc:{
    color:'white'
  },
  blogPostStamp:{
    color:'white'
  },
  postToolBar:{
    display:'flex',
    width:'100%',
  },
  postHeader:{
    display:'flex',
    width:'100%',
  },
  bar:{
    color:'white',
    display: 'flex',
  },
  blog:{
    overflowX:'hidden',
    backgroundColor:'#303030',
    margin: '0',
    fontSize: '1.3rem',
    fontFamily: "OpenSansHebrew",

    direction: 'rtl',
    textAlign: 'right',
  },
  barItem:{
    color:'white',
    paddingLeft:'15px'
  },
  appBar: {
    backgroundColor:'#303030',
    position: 'fixed',
    flexDirection: 'row',
    paddingLeft: '20px',
    color:'white',
    paddingRight: '20px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between'
  },
  overrides: {
    MuiButton: {
      root: {
        textTransform: "none",
      },
      label: {
        color: "#fff",
      },
    },
  },
});
