import {React,useState} from 'react';
import './Blog.css';
import Blog from './Blog';
import { ThemeProvider } from '@material-ui/core/styles';
import theme from "./theme";
import {darkTheme, lightTheme} from "../components/themes";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from '@material-ui/core/TextField';
import Bounce from "react-reveal/Bounce";
import Button from "@material-ui/core/Button";
import Grid from '@material-ui/core/Grid';
import Footer from '../components/Footer.js';
import {makeStyles} from '@material-ui/core/styles';
import {HashLink} from 'react-router-hash-link';
import {Link} from 'react-router-dom';

import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles((theme) => ({
    footerWrap:{
        padding: '30px',

        width:'100%',

        backgroundColor:'#fafafa',
    },
    copyrights:{
    width:'100%',
    textAlign:'center',
    fontSize:'1rem',
    },
    form:{
    paddingTop: '15px'
    },
    formWrap:{
    },
    lastArticlesItem:{
    color:'#686868',
    },
    submit: {
        marginTop: '20px'
    },
}));
function BlogFooter(props){
let social = [
{header:'驻住拽 >',
link:'https://www.facebook.com/profile.php?id=100004539256630'
},
{header:'住专 >',
link:'https://www.instagram.com/ofirthefreelancer/'
},
{header:' >',
link:'https://github.com/ofirthefreelancer/'
},
{header:'爪驻 >',
link:'https://api.whatsapp.com/send?phone=+972544626414&text=砖 (:'
},
];
    const classes = useStyles();
    const [submitted, setSubmitted] = useState(false);
    const [name, setName] = useState("");
    const [lastName, setLastName] = useState("");
    const [email, setEmail] = useState("");
    const [error, setError] = useState(false);
    console.log(props.lastPosts)
    const [message, setMessage] = useState("");
    function sumbitted(event) {
        setError(false);
        event.preventDefault();
        if (name !== "" && lastName !== "" && email !== "" && message !== "") {
            setSubmitted(true);
        } else {
            setError(true);
        }
    }
    return (
        <div className={classes.footerWrap}>
               <Grid  direction="row" alignItems="strech" container
                   spacing={8}>
                <Grid item xs={12} sm={12} md={4} >
                            <div className={classes.formWrap}>
                    <CssBaseline />
                    <div className={classes.paper}>
                        {!submitted && (
                            <Bounce>
                             <strong>
                                <div id="lets-contact">
                                     专
                                </div>
                                </strong>
                                <form
                                    onSubmit={sumbitted}
                                    className={classes.form}
                                    noValidate
                                >
                                    <Grid container spacing={2}>
                                        <Grid item xs={12} sm={6}>
                                            <TextField
                                                autoComplete="fname"
                                                name="firstName"
                                                variant="outlined"
                                                required
                                                onChange={(e) => setName(e.target.value)}
                                                fullWidth
                                                id="firstName"
                                                placeholder="砖 驻专"
                                            />
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <TextField
                                                variant="outlined"
                                                required
                                                fullWidth
                                                id="lastName"
                                                onChange={(e) => setLastName(e.target.value)}
                                                placeholder="砖 砖驻"
                                                name="lastName"
                                                autoComplete="lname"
                                            />
                                        </Grid>
                                        <Grid item xs={12}>
                                            <TextField
                                                variant="outlined"
                                                required
                                                fullWidth
                                                onChange={(e) => setEmail(e.target.value)}
                                                id="email"
                                                placeholder=""
                                                name="email"
                                                autoComplete="email"
                                            />
                                        </Grid>
                                        <Grid item xs={12}>
                                            <TextField
                                                placeholder="转  砖..."
                                                multiline
                                                onChange={(e) => setMessage(e.target.value)}
                                                fullWidth
                                                minRows={10}
                                                maxRows={10}
                                            />
                                        </Grid>
                                    </Grid>
                                    <Button
                                        type="submit"
                                        variant="outlined"
                                        color="secondary"
                                        className={classes.submit}
                                    >
                                        砖
                                    </Button>
                                    {error && (
                                        <Typography>拽 驻专 住专</Typography>
                                    )}
                                </form>
                            </Bounce>
                        )}
                        {submitted && (
                            <div>
                                {" "}
                                <Typography variant="h5">
                                   转 , 驻住 砖 爪.
                                </Typography>
                            </div>
                        )}
                    </div>
                          </div>
                </Grid>

            <Grid item xs={12} sm={4} md={3}>
            <div className={classes.socialLinks}>
               <strong> 住砖 </strong>
                  <div className={classes.lastArticles}>

                             {social && social.length > 0 ? (
                            social.map((social) => {

                            return <div style={{paddingTop:'5px'}}><a target="_blank" href={social.link}  className={classes.lastArticlesItem} > {social.header}</a></div>
                            })) : ''}
                             </div>
            </div>
            </Grid>
            <Grid item xs={12} sm={8} md={5}>
            <div className={classes.lastArticles}>
               <strong> 专 专 </strong>
                <div className={classes.lastArticles}>
                {props.lastPosts && props.lastPosts.length > 0 ? (

               props.lastPosts.map((post) => {

               return <div style={{paddingTop:'5px'}}><HashLink  className={classes.lastArticlesItem} to={`/blog/post/${post.id}`}> {post.postTitle}</HashLink></div>
               })) : ''}
                </div>
            </div>
            </Grid>
            </Grid>
              <div className={classes.copyrights}> 转 砖专转 漏 驻专  2022 </div>
        </div>

    );
}
export default BlogFooter;