import {React, useEffect, useState} from 'react';
import {Link, useParams,useNavigate} from 'react-router-dom';
import firebase from "firebase/compat";
import {getFirestore} from "firebase/firestore/lite";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import NotFound from './NotFound';
import KeyboardReturnIcon from "@material-ui/icons/KeyboardReturn";
import WhatsApp from '../svgs/whatsapp.svg';
import Facebook from '../svgs/facebook.svg';
import {ThemeProvider} from "@material-ui/styles";
import {HashLink} from 'react-router-hash-link';
import monster from '../ills/monster.svg';
import $ from 'jquery';
import {darkTheme, lightTheme} from "../components/themes";
import DarkToggler from '../components/DarkToggler';
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import InstagramIcon from "@material-ui/icons/Instagram";
import FacebookIcon from "@material-ui/icons/Facebook";
import {makeStyles} from "@material-ui/core/styles";

const firebaseConfig = {
    apiKey: "AIzaSyBrjpqZDPafJKS8S4FzG5A-9hqoR3trrFo",
    authDomain: "portfolio-blogger.firebaseapp.com",
    projectId: "portfolio-blogger",
    storageBucket: "portfolio-blogger.appspot.com",
    messagingSenderId: "805454355057",
    appId: "1:805454355057:web:4ade5dd5f02a33e927d9d0"
};
const useStyles = makeStyles((theme) => ({
    appBar: {
        backgroundColor: "#fff",
        position: 'fixed',
        flexDirection: 'row',
        paddingLeft: '20px',
        paddingRight: '20px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
    },
    returnIcon: {},
    hero: {
        height: "500px",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        position: "relative",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        color: "#fff",
        fontSize: "4rem",
        [theme.breakpoints.down("sm")]: {
            height: 300,
            fontSize: "3em"
        }
    },

    blogTitle: {
        fontWeight: 800,
        paddingBottom: theme.spacing(3)
    },
    card: {
        maxWidth: "100%",
    },
    media: {
        height: 240
    },
    cardActions: {
        display: "flex",
        margin: "0 10px",
        justifyContent: "space-between"
    },
    h4: {
        color: 'black'
    },
    arrow: {
        color: 'black',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
    },
    top: {
        width: '100%',

        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
    },
    topItems: {
        display: 'flex',
        alignItems: 'center'
    },
    barItem: {
        paddingLeft: '15px'
    },
    bar: {
        color: 'black',
        display: 'flex',

    },
    monster: {
        padding: '10px'
    },
    author: {
        display: "flex"
    },
    paginationContainer: {
        display: "flex",
        justifyContent: "center"
    }
}));

function BlogPost(props) {

    const [appTheme, setAppTheme] = useState(lightTheme);
    const history = useNavigate();

    function navigateToBlog(id) {
        history(`/blog`);
    }
    const darkHandler = (state) => {
        setAppTheme(state["checkedA"] ? darkTheme : lightTheme);
        if (state["checkedA"]) {
            //dark theme
           // props.setTheme('darkTheme')

            $("html").css("background-color", "#303030")
            $("h2").css("color", "black")
        } else {
           // props.setTheme('lightTheme')

            $("html").css("background-color", "white")
            $("h2").css("color", "inherit")
        }

    };

    const hrefWhatsapp = `whatsapp://send?text=${window.location.href}`;
    const hrefFacebook = `http://www.facebook.com/share.php?u=${window.location.href}`;
    const {id} = useParams();

    let [postData, setPostData] = useState({});

    useEffect(() => {

        const firebaseApp = firebase.initializeApp(firebaseConfig);
        const db = firebase.firestore();
        const firestore = getFirestore()
        db.collection('posts').where(firebase.firestore.FieldPath.documentId(), '==', id).onSnapshot((snapshot) => {
            snapshot.forEach((doc) => setPostData({...doc.data(), id: doc.id}));
        });
    }, []);
    const classes = useStyles();
    // GetPostDataById(id);
    return (
        postData && postData.id ? (

            <ThemeProvider theme={{...appTheme}}>
                <div style={appTheme.blog}>
                    <AppBar style={appTheme.appBar} position="static">
                        <div className={classes.top}>
                            <Toolbar style={appTheme.postToolBar}>
                                <div style={appTheme.postHeader}>
                                    <div>
                                        <div onClick={() => {darkHandler('lightTheme');navigateToBlog()}}>
                                            <KeyboardReturnIcon style={appTheme.arrowColor} fontSize="large"/>
                                        </div>
                                    </div>
                                    <DarkToggler darkModeHandler={darkHandler}/>
                                </div>
                            </Toolbar>
                        </div>
                    </AppBar>
                    <div className="postArticle">
                        <div style={appTheme.blogPostContent}>

                            <div>
                                <h1 style={appTheme.blogPostHeader}>{postData.postTitle}</h1>
                            </div>
                            <div>
                                <h3 style={appTheme.blogPostDesc}>{postData.postDesc}</h3>
                            </div>
                            <div style={appTheme.blogPostStamp}>
                                {postData.postTimestamp}
                            </div>
                            <div style={appTheme.blogPostBody} dangerouslySetInnerHTML={{__html: postData.postBody}}>
                            </div>
                            <div className="postFooter">
                                <h5>
                                    砖  转砖转驻 (:
                                </h5>
                                <div className="share">
                                    <div className="whatsapp">
                                        <a href={hrefWhatsapp} data-action="share/whatsapp/share">
                                            <img alt="whatsapp" src={WhatsApp} height="40"/>
                                        </a>
                                    </div>
                                    <div className="facebook">
                                        <a href={hrefFacebook}>
                                            <img alt="facebook" src={Facebook} height="40"/>
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <br/>
                            <hr/>
                            <br/>
                            <div className="visitMe">
                                <a href="https://www.linkedin.com/in/ofirthedev/">
                                    <LinkedInIcon fontSize="large"/>
                                </a>
                                <a href="https://www.facebook.com/profile.php?id=100004539256630">
                                    <FacebookIcon fontSize="large"/>
                                </a>
                                <a href="https://www.instagram.com/ofirthefreelancer/">
                                    <InstagramIcon fontSize="large"/>
                                </a>
                            </div>
                        </div>

                    </div>

                </div>
            </ThemeProvider>
        ) : <div></div>)
}

export default BlogPost;